import React from 'react';
import styled from 'styled-components';
import Image from 'gatsby-image';

import { HEADER, SUBHEADER } from '../../config';
// import HeroImage from '../../images/hero.jpg';

const Caption = styled.div`
  position: absolute;
  left: 0;
  top: 45%;
  width: 100%;

  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
`;

const CaptionHeader = styled.h1`
  font-weight: 500;
  text-transform: uppercase;
  font-size: 20px;
  letter-spacing: 5.5px;
  color: #ffffff;
  margin: 0px;
`;

const CaptionSubheader = styled.h2`
  margin-top: 5px;
  font-size: 12px;
  letter-spacing: 3.5px;
  font-weight: 500;
  text-transform: uppercase;

  color: #ffffff;
  opacity: 0.35;
`;

const BgImage = styled(Image)`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: -1;
  height: 100vh;

  & > img {
    object-fit: cover !important;
    object-position: 50% 0% !important;
    font-family: 'object-fit: cover !important; object-position: 0% 0% !important;';
  }
`;

const BgOverlay = styled.div`
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #000000;
  opacity: 0.6;
`;

const Container = styled.div`
  position: relative;
`;

const Hero = props => (
  <Container>
    <BgImage sizes={props.file.childImageSharp.sizes} />
    <BgOverlay />
    <Caption>
      <CaptionHeader>{HEADER}</CaptionHeader>
      <CaptionSubheader>{SUBHEADER}</CaptionSubheader>
    </Caption>
  </Container>
);

export const heroImage = graphql`
  fragment HeroImage on Query {
    file(relativePath: { eq: "hero.jpg" }) {
      childImageSharp {
        sizes(maxWidth: 1024) {
          ...GatsbyImageSharpSizes
        }
      }
    }
  }
`;

export default Hero;
